html body{
    background-color: #242429;
    color: #e8e8e8;
    margin: 0;
  
}

/*NAV STYLING*/
nav ul li{
  
    list-style-type: none;
    display: inline-block; 
    font-size: 25px;
    /* margin-left: auto;
    margin-right: auto; */
    /* border-radius: 10px; */
    /* box-shadow: 4px 4px 6px white; */
    /* justify-content: space-between; */
    /* position: fixed;
    top:0; */
    
}

nav{
    width: 100%;
    background-color: orange;
    color: white;
    position: fixed;
    top:0;
    z-index: 9999;
    box-shadow: 0 2px 2px  white;
    text-align: center;
}
nav ul li a{
    margin-right: 25px;
    
}

.activeL{
    color:blue;
}

nav ul li a:hover{
    color:blue;
  
}

nav.active{
    
    /* border-radius: 5px; */
    visibility: visible;
    transition: all 1.5s;
    

}
nav.hidden{

    visibility: hidden;
    transition: all 1.5s;    


}




/*MAIN PAGE STYLING */
.main_page{
    height: auto;
}

.links{
    text-align: center;
}
.linkedin_link{
    font-size: 150px;
    color: skyblue;
   
}
.email_link{
    font-size: 150px;
    color: skyblue;
   
}

.first_name_message{
    font-size: xx-large;
    text-align: center;
}
.contact_info{
    font-size: xx-large;
    text-align: center;
}
.profile_pic{
    height:300px;
    width:300px;
    border-radius: 50%;
    float: center;
    margin-top: -60px;
}
.name{
    font-size: 100px;
    margin-top: 10px;
   
}

.h1_main_page{
    font-size: 50px;
    text-align: center;
}
.greeting{
    margin-top: 100px;
}

.input_first_name {
    width: 200px;
    height: 30px;
    background-color: #ccffff;
    border-radius: 5px;
    font-size: 25px;
    
}

.enter_button{
    width: 210px;
    height: 30px;
    margin-top: 20px;
    margin-left: 2%;
    font-size: 20px;
    background-color: black;
    color: white;
    border-radius: 5px;
    border-color: skyblue;
    outline: none;
}
.first_name_form{
    text-align: center;
}




/*SKILSS STYLING*/
.skills{
    height: auto;
}

.icons_list{
    text-align: center;
    font-size: 28px;
}

.icons{
    color: orange;
    font-size: 100px;
}

.others_list{
    font-size: 20px;
    text-align: center;
    margin-top: -50px;
}



/*EDUCATION STYLING*/
.education{
    height: auto;
    margin-top: 70px;
}
.uoft_logo{
    float:right;
    
}
.uoft_logo2{
    float: left;
}
.education_name{
    text-align: center;
    font-size: 20px;
}
.education_description{
    margin-left:5%;
    font-size: 20px;

}
.desc_button{
    display: block;
    margin: 0 auto;
    font-size: 75px;
    border:none;
    background-color: #242429;
    color: skyblue;
    outline: none;
}

/*WORK EXPERIENCE STYLING*/

.work_experience{
    height: auto;
    margin-top: 70px;
    padding-bottom: 100px;
}
.work_name{
    text-align: center;
    font-size: 25px;
  
}
.work_description{
    text-align: center;
    font-size: 20px;

}

.swim_icon1{
    margin-top: 50px;
    float: left;
    font-size: 80px;
    color: skyblue;
}
.swim_icon2{
    float: right;
    margin-top: 50px;

    font-size: 80px;
    color: skyblue;
}
.desc_button_we{
    display: block;
    margin: 0 auto;
    font-size: 75px;
    border:none;
    background-color: #242429;
    color: skyblue;
    outline: none;
}



/*AGENDA STYLING*/


.agenda_project{
    height:100vh;
    overflow: scroll;
    width:170vh;
    background-color: beige;
    margin-left: auto;
    margin-right: auto;
    border-radius: 10px;
    padding-bottom: 100px;
    color: rgb(255, 94, 0);
}
.h1_agenda{
    font-size: 50px;
    
}

.task_input{
    display: block;
    margin: 0 auto;
    margin-top: 1%;
    width:360px;
    height: 25px;
    font-size: 20px;
    border-left: none;
    border-right: none;
    border-top: none;
    border-bottom-color: black;
    border-bottom-width: thin;
    background-color: beige;
}

.button_add{
    display: block;
    margin: 0 auto;
    width:378px;
    height:30px;
    background-color: black;
    color: skyblue;
    font-size: 20px;
    border-color: skyblue;
    border-radius:10px;
    outline: none;
}
.button_clear{
   
    display: block;
    margin: 0 auto;
    width:378px;
    height:30px;
    background-color: black;
    color: skyblue;
    font-size: 20px;
    border-color: skyblue;
    border-radius:10px;
    outline: none;
}
.h1_agenda{
    text-align: center;
    padding-top: 30px;
}
.display_todo{
    text-align: center;
    font-size: 30px;
    margin-top: 20px;
}
.time_popup{
    color: black;
    font-size: 20px;
    
}
.time_picker{
    width: 360px;
    display:block;
    margin: 0 auto;
    

}
.rc-time-picker-input {
    padding:0;
    font-size: 20px;
    border-left: none;
    border-right: none;
    border-top: none;
    border-radius: 0;
    border-bottom-color: black;
    border-bottom-width: thin;
    background-color: beige;
  }
.rc-time-picker-panel-select{
    font-size: 15px;
    width: 100px;
}
.rc-time-picker-panel{
    width: 300px;
}
.rc-time-picker-clear-icon:after {
    font-size: 20px;
    color: black;
}


/*JUG PUZZLE STYLING */
.jug_puzzle{
    height:auto;
    padding-top: 100px;
    
}   

.form_jug{
    text-align: center;
}

.jug_status{
    text-align: center;
    margin-top: 8%;
    
}
.select_move{
    width: 180px;
    height: 30px;
    font-size: 20px;
    background-color: #ccffff;
    border-radius: 5px;
}
.reset_button{
    width: 180px;
    height: 30px;
    font-size: 20px;
    background-color: black;
    color:skyblue;
    border-color: skyblue;
    border-radius:10px;
    outline: none;
}
.jug_instructions{
    text-align: center;
    
}
.drop{
    color: #0099ff;
}





.display_box{
    font-family: "Raleway";
    background-color: white;
    color: black;
    width: 700px;
    height: 530px;
    overflow: scroll;
    margin: auto;
    margin-top: 100px;
    border-radius: 10px;
    box-shadow: 0 10px 10px  rgb(255, 94, 0);
    
}
.display_content{
    margin-left: 20px;
}

.profile_pic_student{
    border-style: solid;
    border-radius: 50%;
    border-width: 0.1em;
    width:100px;
    height:100px;
    margin-top: 30px;

}
.name_student{
    text-transform: uppercase;
    margin-left: 150px;
    margin-top: -125px;
    font-size: 30px;
}
.email, .company, .skill, .average{
    margin-left: 170px;
    line-height: 2px;
}

.name_search {
    width: 95%;
    border-top: none;
    border-left: none;
    border-right:none;
    height:50px;
    margin-left: 15px;
    font-size: 20px;
}

.expand_button{
    float:right;
    margin-right: 20px;
    margin-top: -140px;
    font-size: 50px;
    border: none;
    background-color: white;
}
.grades{
    margin-left: 170px;
    line-height: 4px;
    padding-bottom: 10px;
}
.tag_search{
    width: 95%;
    border-top: none;
    border-left: none;
    border-right:none;
    height:50px;
    margin-left: 15px;
    font-size: 20px;
}
.tag_display{
    display: inline;
    border-radius: 5px;
    padding:8px;
    background-color: lightgray;
    margin-left: 10px;
}
.add_tag{
    margin-left: 170px;
    margin-top: 20px;
    border-left: none;
    border-top: none;
    border-right: none;
    font-size: 18px;
}
.tag_display_container{
    margin-left: 180px;
}







